export class JobItemGroupViewController extends BaseTreeViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#job_item_group_view_container',



			tree_mode: null,
			grid_table_name: null,
			grid_select_id_array: null
		} );

		super( options );
	}

	init( options ) {
		//this._super('initialize', options );
		this.edit_view_tpl = 'JobItemGroupEditView.html';
		this.permission_id = 'job_item';
		this.viewId = 'JobItemGroup';
		this.script_name = 'JobItemGroupView';
		this.table_name_key = 'job_item_group';
		this.context_menu_name = $.i18n._( 'Task Groups' );
		this.grid_table_name = $.i18n._( 'Task Group' );
		this.navigation_label = $.i18n._( 'Task Group' );

		this.tree_mode = true;
		this.primary_tab_label = $.i18n._( 'Task Group' );
		this.primary_tab_key = 'tab_task_group';

		this.api = TTAPI.APIJobItemGroup;
		this.grid_select_id_array = [];

		this.render();
		this.buildContextMenu();
		this.initData();
	}

	getCustomContextMenuModel() {
		var context_menu_model = {
			exclude: [
				'copy',
				'mass_edit',
				'delete_and_next',
				'save_and_continue',
				'save_and_next',
				'export_excel'
			],
			include: []
		};

		return context_menu_model;
	}

}